html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

:root {
  --main-color: #00acdc;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  height: 60px;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active {
  color: var(--main-color);
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active .nav__link {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--main-color);
  -webkit-text-decoration-color: var(--main-color);
  text-decoration-color: var(--main-color);
  height: 60px;
  line-height: 60px;
  text-decoration-thickness: 2px;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav__list li:last-child a {
  margin-right: 0;
}
